import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)
const store = new Vuex.Store({
    state: {
        todolist: [
            {
                id: 1,
                done: true,
                title: "3"
            },
            {
                id: 2,
                done: false,
                title: "4"
            },
            {
                id: 3,
                done: false,
                title: ""
            },
                {
                id: 4,
                done: false,
                title: ""
            }
        ]
    },
  mutations: {
  }
})

export default store


